/** */
export const getGameMapConfig = (urlIndex: number = 0): Promise<GameConfigType> => {
  // Mock  mock 
  if (DEV) {
    console.log("[Mock]  mock ");
    return Promise.resolve(mockGameConfig);
  }

  return new Promise((reslove, reject) => {
    console.log("", GameConfig.gameConfigUrl[urlIndex])
    fetch(`${GameConfig.gameConfigUrl[urlIndex]}?t=${Date.parse(new Date() as any)}`).then((response) => {
      console.log("", response)
      if (response.status === 200) {
        // response.text() returns a Promise and consumes the body. It can only be called once.
        response.text().then(jsonStr => {
          console.log(" text", jsonStr)
          try {
            const jsonObj = JSON.parse(jsonStr.trim())
            console.log(" json", jsonObj)
            reslove(jsonObj)
          } catch (e) {
            console.error("", e)
            reject(e)
          }
        }).catch(e => reject(e))
      } else {
        const _urlIndex = urlIndex + 1
        if (_urlIndex === GameConfig.gameConfigUrl.length) {
          reject('config_error')
        } else {
          reslove(getGameMapConfig(_urlIndex))
        }
      }
    }).catch((e) => {
      const _urlIndex = urlIndex + 1
      if (_urlIndex === GameConfig.gameConfigUrl.length) {
        reject('config_error')
      } else {
        reslove(getGameMapConfig(_urlIndex))
      }
    })
  })
}
