export const getIsTest = () => {
  /** */
  const pkgCode = getPackageName()
  const isDev = DEV || pkgCode.includes('test') || pkgCode.includes('development') || pkgCode.includes('advance')// || (!DEV && pkgCode.includes('web'))
  // console.log('isDev=' + isDev, DEV, pkgCode)
  return isDev
}
export const getEnvKey = () => {
  const pkgCode = getPackageName()
  console.log("pkgCode", pkgCode)
  if (pkgCode.includes("development")) return "development"
  else if (pkgCode.includes("test")) return "test"
  else if (pkgCode.includes("advance")) return "advance"
  //
  else if (pkgCode.includes("web") && DEV) return "test"
  else return "production"
}
/**H5 */
