/**
 * Hall
 *  index.ts 
 */

import { AssetManager, Node } from "cc";
import SourceManage from "../base/SourceManage";
import BaseBoardViewModel from "./viewModel/BaseBoardViewModel";
import { AudioMgr } from "../utils/AudioMgr";
import { SoundPathDefine } from "./sourceDefine/soundDefine";
import { SoundPathDefine as CommonSoundPathDefine } from "../common/sourceDefine/soundDefine";
import Fetcher from "../utils/Fetcher";
import { ApiUrl } from "./apiUrl";
import Internationalization from "../language/Internationalization";
import languagePkg from "../language/languagePkg";
import { getStore } from "./store";
import { AnyAction } from "redux";

// =====  =====

export let sourceManageMap: Array<SourceManage> = [];
export let rootNodeWrap: Node;
export let baseBoardView: BaseBoardViewModel;
export let bundleCommon: AssetManager.Bundle = null;
export let bundleHall: AssetManager.Bundle = null;
export let hallAudio: AudioMgr<SoundPathDefine> = null;
export let commonAudio: AudioMgr<CommonSoundPathDefine> = null;
export let fetcher: Fetcher<ApiUrl>;

// =====  =====

export const sourceManageSeletor = (bundleName: string = "hall") =>
  sourceManageMap.find((i) => i.bundle.name === bundleName);

/**
 * Redux storedispatch
 */
export const hallDispatch = (action: AnyAction) => {
  getStore().dispatch(action);
};

// =====  =====

export const lang = new Internationalization(languagePkg);

// ===== API () =====

/** */
export const global = {
  /**store */
  hallDispatch: hallDispatch,
  /**
   * 
   * @param isPre 
   * @param confirmContent 
   * @param confirmDoneAfterFn 
   * @param confirmDoneBeforeFn 
   */
  closeSubGame: async (option?: {
    isPre?: boolean;
    confirmContent?: string;
    confirmDoneAfterFn?: Function;
    confirmDoneBeforeFn?: Function;
  }) => {
    const _option = Object.assign(
      {
        isPre: false,
        confirmContent: "",
        confirmDoneAfterFn: () => { },
        confirmDoneBeforeFn: () => { },
      },
      option || {}
    );

    //  ModalBox, hallWebSocketDriver, TaskSchedulerDefault
    const [{ default: ModalBox }, { hallWebSocketDriver, SKT_MAG_TYPE }, { TaskSchedulerDefault }] = await Promise.all([
      import("../common/ModalBox"),
      import("./socketConnect"),
      import("../utils/TaskScheduler")
    ]);

    //
    if (_option.confirmContent) {
      if (
        !ModalBox.Instance().isShow &&
        _option.confirmContent !== ModalBox.Instance().contentStr
      ) {
        ModalBox.Instance().show(
          { content: _option.confirmContent, type: "Prompt" },
          () => {
            _option.confirmDoneBeforeFn();
            baseBoardView.comp && baseBoardView.comp.closeSubGame();
            _option.confirmDoneAfterFn();
            return true;
          }
        );
      }
    } else {
      baseBoardView.comp && baseBoardView.comp.closeSubGame();
    }
    TaskSchedulerDefault().stopQueue(false);
    hallWebSocketDriver.sendSktMessage(SKT_MAG_TYPE.MEMBER_INFO, "", {
      isLoading: false,
    });
  },
  /** */
  openShop: (buyType?: any) => {
    baseBoardView && baseBoardView.mainPanelViewModel.openShop(buyType);
  },
  /** */
  openPersonCenter: (index?: number) => {
    baseBoardView && baseBoardView.mainPanelViewModel.openPc(index);
  },
  /**vip */
  openVipMain: () => {
    baseBoardView && baseBoardView.mainPanelViewModel.openVipMain();
  },
  openSign: () => {
    baseBoardView && baseBoardView.mainPanelViewModel.openSign();
  },

  /** */
  loadHeadSprite: async (icon: number, sprite: any) => {
    const { SpriteFrame } = await import("cc");
    bundleCommon &&
      bundleCommon.load(
        `resource/head/head_circle_${icon}/spriteFrame`,
        SpriteFrame,
        (err, sp) => {
          if (!err) {
            sprite.spriteFrame = sp;
          }
        }
      );
  },
  /** */
  setSubGameGate: (gameId: number, progress: number) => {
    baseBoardView &&
      baseBoardView.mainPanelViewModel.comp.setSubGameGate(gameId, progress);
  },
  /**, */
  isAllowOpenSubGame: (gameId: number) => {
    if (baseBoardView) {
      return baseBoardView.isAllowOpenSubGame(gameId);
    }
    return true;
  },

  showGiftWindow: async (gameType: any, userInfo: any | null, callback: any) => {
    const { default: GiftUserViewModel } = await import("../common/viewModel/GiftUserViewModel");
    if (rootNodeWrap && rootNodeWrap.isValid) {
      GiftUserViewModel.show(rootNodeWrap, gameType, userInfo, callback);
    }
  },
  flyGift: async (
    fromMemberId: string,
    toMemberId: string,
    startPos: any,
    endPos: any,
    value: number,
    parentNode: Node
  ) => {
    const { default: GiftUserViewModel } = await import("../common/viewModel/GiftUserViewModel");
    if (!parentNode) {
      parentNode = rootNodeWrap;
    }
    if (parentNode && parentNode.isValid) {
      GiftUserViewModel.flyGift(
        parentNode,
        fromMemberId,
        toMemberId,
        startPos,
        endPos,
        value
      );
    }
  },
};

// ===== Setter index.ts =====

export const setSourceManageMap = (value: Array<SourceManage>) => {
  sourceManageMap = value;
};

export const setRootNodeWrap = (value: Node) => {
  rootNodeWrap = value;
};

export const setBaseBoardView = (value: BaseBoardViewModel) => {
  baseBoardView = value;
};

export const setBundleCommon = (value: AssetManager.Bundle) => {
  bundleCommon = value;
};

export const setBundleHall = (value: AssetManager.Bundle) => {
  bundleHall = value;
};

export const setHallAudio = (value: AudioMgr<SoundPathDefine>) => {
  hallAudio = value;
};

export const setCommonAudio = (value: AudioMgr<CommonSoundPathDefine>) => {
  commonAudio = value;
};

export const setFetcher = (value: Fetcher<ApiUrl>) => {
  fetcher = value;
};
