import { ActionTypes, ActionPayLoad } from "../actions/memberInfo";
import { MemberInfoInitStateType } from '../types';
import reduxAct from 'redux-act'

import { defaultLanguageType } from "../../../language/languagePkg";
import { config } from "../../hallConfig";

const initState: MemberInfoInitStateType = {
  memberName: "",
  avatarIndex: 0,
  memberAssetGoldPieces: 0,
  memberAssetSafeGoldPieces: 0,
  memberId: '',
  nickName: '',
  vipLevel: 0,
  vipLevelExperience: 0,
  gender: 0,
  level: 0,
  levelExperience: 0,
  phone: "",
  tourist: 1,
  memberRechargeWater: 0,
  minimumWithdrawal: 0,
  totalFlowingWater: 0,
  canExtractRebateGold: 0,
  rebateScale: 0,
  totalReturnWaterAmount: 0,
  flowingWater: 0,
  isUnreadActivity: false,
}

export default reduxAct.createReducer<MemberInfoInitStateType>({
  [ActionTypes.SET_MEMBER_INFO]: (state, payload: ActionPayLoad<ActionTypes.SET_MEMBER_INFO>): MemberInfoInitStateType => {
    //  phoneAreaNum
    const phoneAreaNum = defaultLanguageType[config.country]?.phoneAreaNum || "";
    return {
      ...state,
      memberName: payload.memberName && phoneAreaNum ? payload.memberName.replace(new RegExp(`^${phoneAreaNum}`, 'g'), "") : payload.memberName,
    avatarIndex: payload.avatarIndex,
    memberAssetGoldPieces: payload.memberAssetGoldPieces,
    memberAssetSafeGoldPieces: payload.memberAssetSafeGoldPieces,
    memberId: payload.memberId,
    nickName: payload.nickName,
    vipLevel: payload.vipLevel,
    level: payload.level,
    phone: payload.phone,
    gender: payload.gender,
    vipLevelExperience: payload.vipLevelExperience,
    levelExperience: payload.levelExperience,
    tourist: payload.tourist,
    memberRechargeWater: payload.memberRechargeWater,
    minimumWithdrawal: payload.minimumWithdrawal,
    canExtractRebateGold: payload.canExtractRebateGold,
    rebateScale: payload.rebateScale,
    totalReturnWaterAmount: payload.totalReturnWaterAmount,
    totalFlowingWater: payload.totalFlowingWater,
    flowingWater: payload.flowingWater
    }
  },
  [ActionTypes.RESET_MEMBER_INFO_DATA]: (state): MemberInfoInitStateType => ({
    ...Object.assign({}, initState)
  }),
  [ActionTypes.SET_VIP]: (state, payload: ActionPayLoad<ActionTypes.SET_VIP>): MemberInfoInitStateType => {
    return {
      ...state,
      vipLevel: payload.vipLevel,
      vipLevelExperience: payload.vipLevelExperience
    }
  },
  [ActionTypes.CHANGE_GOLD]: (state, payload: ActionPayLoad<ActionTypes.CHANGE_GOLD>): MemberInfoInitStateType => {
    return {
      ...state,
      memberAssetGoldPieces: state.memberAssetGoldPieces + payload.value,
    }
  },
  [ActionTypes.UPDATE_ACTIVITY_STATUS]: (state, payload: ActionPayLoad<ActionTypes.UPDATE_ACTIVITY_STATUS>): MemberInfoInitStateType => {
    return {
      ...state,
      isUnreadActivity: payload.isUnreadActivity
    }
  },

}, initState);

