import { instantiate } from "cc";
import { ActionTypes, ActionPayLoad } from "../actions/baseBoard";
import { BaseBoardInitStateType, ToastType, ToastPosition } from '../types';
import reduxAct from 'redux-act'

const initState: BaseBoardInitStateType = {
  toastData: { content: "", type: ToastType.NORMAL, position: ToastPosition.MIDDLE, forceLandscape: false },
  loadPayload: { isShow: false, isAllowCloseLoading: false, flagId: '_' },
  subGameInfo: null,
  isShowWebView: true,
  isLoadRecommendData: '',
  isConnect: true,
  remainRetryCount: 0,
  appDownLoadGuide: true,
  UnreadMailNum: -1,
  vipBonusInfo: null,
  chooseAmount: null,
}

export default reduxAct.createReducer<BaseBoardInitStateType>({
  [ActionTypes.ADD_TOAST]: (state, payload: ActionPayLoad<ActionTypes.ADD_TOAST>): BaseBoardInitStateType => {
    return ({
      ...state,
      toastData: payload.toastData
    })
    // state.toastData = { content: "", type: state.toastData.type }
    // return result

    // const _state = instantiate(state)
    // _state.toastData = { ...payload.toastData }
    // console.log('reducer',_state)
    // return _state
  },
  [ActionTypes.SET_LOADING]: (state, payload: ActionPayLoad<ActionTypes.SET_LOADING>): BaseBoardInitStateType => {
    return {
      ...state,
      loadPayload: Object.assign({ isLoadPayload: false, flagId: '_', isAllowCloseLoading: false }, payload.isLoadPayload),
    }
  },
  [ActionTypes.SET_ACTIVE_SUBGAME]: (state, payload: ActionPayLoad<ActionTypes.SET_ACTIVE_SUBGAME>): BaseBoardInitStateType => {
    return {
      ...state,
      subGameInfo: payload.subGameInfo
    }
  },
  [ActionTypes.SET_SUBGAME_STATE]: (state, payload: ActionPayLoad<ActionTypes.SET_SUBGAME_STATE>): BaseBoardInitStateType => {
    const _state = instantiate(state)
    _state.subGameInfo && (_state.subGameInfo.runState = payload.subGameRunState)
    return _state
  },
  [ActionTypes.CHANGE_WEB_VIEW]: (state, payload: ActionPayLoad<ActionTypes.CHANGE_WEB_VIEW>): BaseBoardInitStateType => {
    return {
      ...state,
      isShowWebView: payload.value
    }
  },
  [ActionTypes.LOAD_RECOMMEND_DATA]: (state, payload: ActionPayLoad<ActionTypes.LOAD_RECOMMEND_DATA>): BaseBoardInitStateType => {
    return {
      ...state,
      isLoadRecommendData: Date.parse(new Date().toString()).toString()
    }
  },
  [ActionTypes.APP_DOWNLOAD_GUIDE]: (state, payload: ActionPayLoad<ActionTypes.APP_DOWNLOAD_GUIDE>): BaseBoardInitStateType => {
    return {
      ...state,
      appDownLoadGuide: payload.appDownLoadGuide
    }
  },
  [ActionTypes.SOCKET_CONNECT]: (state, payload: ActionPayLoad<ActionTypes.SOCKET_CONNECT>): BaseBoardInitStateType => {
    if (state.remainRetryCount === undefined) { }
    return {
      ...state,
      isConnect: payload.connectData.isConnect,
      remainRetryCount: payload.connectData.remainRetryCount
      // remainRetryCount: (payload.connectData.remainRetryCount === undefined ? (state.remainRetryCount + 1) : payload.connectData.remainRetryCount)
    }
  },
  [ActionTypes.RESET_DATA_BOARD]: (): BaseBoardInitStateType => instantiate(initState),
  [ActionTypes.UPDATE_MAIL_STATUS]: (state, payload: ActionPayLoad<ActionTypes.UPDATE_MAIL_STATUS>): BaseBoardInitStateType => {
    return {
      ...state,
      UnreadMailNum: payload.UnreadMailNum
    }
  },
  [ActionTypes.UPDATE_VIP_BONUSINFO]: (state, payload: ActionPayLoad<ActionTypes.UPDATE_VIP_BONUSINFO>): BaseBoardInitStateType => {
    return {
      ...state,
      vipBonusInfo: payload.vipBonusInfo
    }
  },
  [ActionTypes.TOP_UP_CHOOSE_AMOUNT]: (state, payload: ActionPayLoad<ActionTypes.TOP_UP_CHOOSE_AMOUNT>): BaseBoardInitStateType => {
    return {
      ...state,
      chooseAmount: payload.chooseAmount
    }
  },
}, initState);