import { initConfig, subGameList, HallGameGateType } from "./config"
import { config as hallConfig, SKT_OPERATION, EVEVT_TYPE } from "./hallConfig"
import { ToastType, addToastAction, setSocketConnectStatus } from "./store/actions/baseBoard"
import { baseBoardView, lang, hallDispatch } from "./globals"
import WebSocketStarter, { WebSocketDriver } from "../common/WebSocketStarter"
import ModalBox from "../common/ModalBox"
import { Task, TaskSchedulerDefault } from "../utils/TaskScheduler"

// 
export { SKT_OPERATION, EVEVT_TYPE }

export enum SKT_MAG_TYPE {
  /** */
  LOG_EXIT = "0",
  /** */
  HEART_BEAT = "1",
  /** */
  AUTH = "2",
  /** */
  EXIT = "3",
  /** */
  GET_WITH_DRAW_CHANNEL = "411",
  /** */
  RECHARGE_ORDER = "412",
  /** */
  MEMBER_INFO = "400",
  /** */
  MODIFY_MEMBER_INFO = "416",
  /** */
  MODIFY_BIND_PHONE = "408",
  /** */
  NEW_MAIL = "409",
  /** */
  POP_UPS = "403",
  /** */
  MAEQUEE = "404",
  /** */
  WITH_DRAW_LIST = "419",
  /**VIP */
  VIP_CHANGE = "420",
  /** */
  GIVE_GLOD = "423",
  /** */
  GIFT_LIST = "424",
  /** */
  WINNING_BOX = "425",
  /** */
  TURNTABLEDATA = "426",
  /** */
  TURNTABLESEND = "427",
  /** */
  TURNTABLETNOTIFICATION = "428",
  /** */
  RECHANGE_CALLBACK = "429",
  /** */
  FRIST_LOGIN = "430"
}

// export const sktMsgListener = listenerFactoy<SKT_MAG_TYPE>()
// export let sktInstance: WebSocketToDo<SKT_MAG_TYPE> = null
//  hallConfig  hallWebSocketDriver
// hallConfig  hall-config.ts 
export let hallWebSocketDriver: WebSocketDriver<SKT_MAG_TYPE> = new WebSocketDriver<SKT_MAG_TYPE>(hallConfig.gameId, hallConfig.gameHost);

const setupHallWebSocketDriverFilter = () => {
  hallWebSocketDriver.filterData = (data, source) => {
    if (source.operation === SKT_OPERATION.RECOVER && baseBoardView) {
      if (!baseBoardView.comp.props.openGameInfo) {
        const hallGameGate: HallGameGateType = subGameList.find(i => i.gameId === source.gameId)
        if (hallGameGate) {
          const openResetConfirm = (callback?: Function) => {
            window.setTimeout(() => {
              ModalBox.Instance().show({ content: lang.write(k => k.HallModule.UnfinishedGames, { game: hallGameGate.gameName }, { placeStr: "" }), type: "Prompt" }, () => {
                try {
                  const gateViewModel = baseBoardView.mainPanelViewModel.comp.gateViewModelList.find(vm => vm.comp.props.gamesIds.indexOf(hallGameGate.gameId) !== -1)
                  if (gateViewModel && gateViewModel.comp) {
                    gateViewModel.comp.openGateGame(hallGameGate.gameId)
                  } else {
                    hallDispatch(addToastAction({ content: `gameId error:${source.gameId}`, type: ToastType.ERROR, forceLandscape: false }))
                  }
                } catch (e) {
                  hallDispatch(addToastAction({ content: e, type: ToastType.ERROR, forceLandscape: false }))
                }
                callback && callback()
                return true
              })
            }, 1000)
          }
          if (baseBoardView.mainPanelViewModel && baseBoardView.mainPanelViewModel.isBeginPop) {
            //
            TaskSchedulerDefault().joinQueue(new Task((done) => {
              openResetConfirm(done)
            }))
          } else {
            //der
            baseBoardView.mainPanelViewModel && (baseBoardView.mainPanelViewModel.isBeginPop = true)
            openResetConfirm()
          }
        }
      } else {
        //LOGIN
      }
      return
    }
    if (data.code === '200') {
      return {
        data: data.data,
        error: undefined
      }
    } else {
      console.error(data, source)
      hallDispatch(addToastAction({ content: `${data.message}[${data.code}]`, type: ToastType.ERROR, forceLandscape: false }))
      return {
        data: '', error: data.message
      }
    }
  }
}

//  filterData
setupHallWebSocketDriverFilter();

export default () => {
  return new Promise((resolve, reject) => {
    initConfig().then(() => {
      //  WebSocket Driver 
      hallWebSocketDriver.gameId = hallConfig.gameId;
      hallWebSocketDriver.host = hallConfig.gameHost;
      WebSocketStarter.Instance().initSocket().then(() => {
        const wss = WebSocketStarter.Instance()
        wss.eventListener.add(EVEVT_TYPE.HEART_BEAT, '', () => {
          if (!wss.isConnect || wss.isReconnecting) {
            hallDispatch(setSocketConnectStatus({ isConnect: true, remainRetryCount: wss.maxReconnectTime }))
          }
        })
        wss.eventListener.add(EVEVT_TYPE.DISCONNECT, '', () => {
          hallDispatch(setSocketConnectStatus({ isConnect: false, remainRetryCount: wss.remainRetryCount }))
        })
        wss.eventListener.add(EVEVT_TYPE.OPEN, '', () => {
          hallDispatch(setSocketConnectStatus({ isConnect: true, remainRetryCount: wss.maxReconnectTime }))
        })
        wss.eventListener.add(EVEVT_TYPE.WARN, '', () => {
          hallDispatch(addToastAction({ content: lang.write(k => k.WebSocketModule.NetworkInstability, {}, { placeStr: "" }), type: ToastType.ERROR, forceLandscape: false }))
        })
        wss.eventListener.add(EVEVT_TYPE.RECONNECT, '', (remainRetryCount) => {
          hallDispatch(setSocketConnectStatus({ isConnect: false, remainRetryCount }))
        })
        resolve(hallWebSocketDriver)
      })
    }).catch((e) => {
      reject(e)
    })
  })
}

