import reduxAct from 'redux-act';
import { MemberInfoInitStateType as InitStateType } from '../types';

export type { InitStateType }

/**redux-act */
export enum ActionTypes {
  SET_MEMBER_INFO = 'SET_MEMBER_INFO',
  RESET_MEMBER_INFO_DATA = 'RESET_MEMBER_INFO_DATA',
  SET_VIP = 'SET_VIP',
  CHANGE_GOLD = 'CHANGE_GOLD',
  UPDATE_ACTIVITY_STATUS = 'UPDATE_ACTIVITY_STATUS',

}

/**actionpayLoad */
export type ActionPayLoad<A extends ActionTypes> =
  A extends ActionTypes.SET_MEMBER_INFO ? InitStateType :
  A extends ActionTypes.SET_VIP ? { vipLevel: number, vipLevelExperience: number } :
  A extends ActionTypes.CHANGE_GOLD ? { value: number } :
  A extends ActionTypes.UPDATE_ACTIVITY_STATUS ? { isUnreadActivity: boolean } :

  never;


export const setMemberInfo = reduxAct.createAction(ActionTypes.SET_MEMBER_INFO,
  (memberInfo: {
    memberName: string,
    avatarIndex: number,
    memberAssetGoldPieces: number,
    memberAssetSafeGoldPieces: number,
    memberId: string,
    nickName: string,
    vipLevel: number,
    vipLevelExperience: number,
    gender: number,
    level: number,
    levelExperience: number,
    phone: string,
    tourist: number,
    /** */
    memberRechargeWater: number,
    /** */
    minimumWithdrawal: number,
    /** */
    totalFlowingWater: number,
    /** */
    canExtractRebateGold: number,
    /** */
    rebateScale: number,
    /** */
    totalReturnWaterAmount: number,
    /** */
    flowingWater: number,
    /** */
    isUnreadActivity: boolean,
  }): ActionPayLoad<ActionTypes.SET_MEMBER_INFO> => {
    return { ...memberInfo }
  })

export const resetMemberInfo = reduxAct.createAction(ActionTypes.RESET_MEMBER_INFO_DATA, (): any => { })
export const setVip = reduxAct.createAction(ActionTypes.SET_VIP,
  (vipLevel: number, vipLevelExperience: number): ActionPayLoad<ActionTypes.SET_VIP> => {
    return { vipLevel, vipLevelExperience }
  });
export const changeGold = reduxAct.createAction(ActionTypes.CHANGE_GOLD,
  (value: number): ActionPayLoad<ActionTypes.CHANGE_GOLD> => {
    return { value }
  });
export const updateActivityStatus = reduxAct.createAction(ActionTypes.UPDATE_ACTIVITY_STATUS,
  (isUnreadActivity: boolean): ActionPayLoad<ActionTypes.UPDATE_ACTIVITY_STATUS> => {
    return { isUnreadActivity }
  });
