import { instantiate } from "cc";
import { ActionTypes, ActionPayLoad } from "../actions/withdraw";
import { WithdrawInitStateType } from '../types';
import reduxAct from 'redux-act'

const initState: WithdrawInitStateType = {
  withdrawBankChannelChoose: null,
  withdrawBankChannelList: []
}

export default reduxAct.createReducer<WithdrawInitStateType>({
  [ActionTypes.SET_BANK_CHANNEL_LIST]: (state, payload: ActionPayLoad<ActionTypes.SET_BANK_CHANNEL_LIST>): WithdrawInitStateType => ({
    ...state,
    withdrawBankChannelList: payload.list
  }),
  [ActionTypes.SET_BANK_CHANNEL_CHOOSE]: (state, payload: ActionPayLoad<ActionTypes.SET_BANK_CHANNEL_CHOOSE>): WithdrawInitStateType => {
    return {
      ...state,
      withdrawBankChannelChoose: payload.item
    }
  },
  [ActionTypes.RESET_WITHDRAW_DATA]: (): WithdrawInitStateType => instantiate(initState)
}, initState);