import { _decorator, Component, Node, RichText, SpriteAtlas, UITransform } from "cc";

const { ccclass, property } = _decorator;

export class SimpleHtmlParser {
    /**
     *  HTML  Cocos RichText  BBCode 
     */
    static toRichText(html: string): string {
        if (!html) return "";

        //  <br> 
        let processedHtml = html.replace(/<br\s*\/?>/gi, '\n');

        // 
        const tokenRegex = /<[^>]+>|[^<]+/g;
        const tokens = processedHtml.match(tokenRegex) || [];

        let result = "";
        const tagStack: string[] = [];

        for (const token of tokens) {
            if (token.startsWith('<')) {
                // 
                if (token.startsWith('</')) {
                    // 
                    //  BBCode
                    //  HTML 
                    //  BBCode 
                    //  closing tag 

                    // 
                    //  BBCode ( <color=...>),  </color> 
                    // 

                    const closingTags = tagStack.pop();
                    if (closingTags) {
                        result += closingTags;
                    }
                } else {
                    // 
                    const tagNameMatch = token.match(/<([a-zA-Z0-9]+)/);
                    if (!tagNameMatch) continue;

                    const tagName = tagNameMatch[1].toLowerCase();
                    const isSelfClosing = token.endsWith('/>');

                    // 
                    const attrs: any = {};
                    const attrRegex = /([a-zA-Z0-9-]+)=['"]([^'"]*)['"]/g;
                    let attrMatch;
                    while ((attrMatch = attrRegex.exec(token)) !== null) {
                        attrs[attrMatch[1]] = attrMatch[2];
                    }

                    let openTags = "";
                    let closeTags = "";

                    // 1.  style 
                    if (attrs.style) {
                        const style = attrs.style.toLowerCase();

                        // color
                        const colorMatch = style.match(/color:\s*([^;]+)/);
                        if (colorMatch) {
                            openTags += `<color=${colorMatch[1].trim()}>`;
                            closeTags = `</color>` + closeTags;
                        }

                        // font-size
                        const sizeMatch = style.match(/font-size:\s*(\d+)/);
                        if (sizeMatch) {
                            openTags += `<size=${sizeMatch[1]}>`;
                            closeTags = `</size>` + closeTags;
                        }

                        // font-weight: bold
                        if (style.includes('font-weight:bold') || style.includes('font-weight: bold')) {
                            openTags += `<b>`;
                            closeTags = `</b>` + closeTags;
                        }

                        // font-style: italic
                        if (style.includes('font-style:italic') || style.includes('font-style: italic')) {
                            openTags += `<i>`;
                            closeTags = `</i>` + closeTags;
                        }

                        // text-decoration: underline
                        if (style.includes('text-decoration:underline') || style.includes('text-decoration: underline')) {
                            openTags += `<u>`;
                            closeTags = `</u>` + closeTags;
                        }
                    }

                    // 2. 
                    if (tagName === 'font') {
                        if (attrs.color) {
                            openTags += `<color=${attrs.color}>`;
                            closeTags = `</color>` + closeTags;
                        }
                        if (attrs.size) {
                            openTags += `<size=${attrs.size}>`;
                            closeTags = `</size>` + closeTags;
                        }
                    } else if (tagName === 'b' || tagName === 'strong') {
                        openTags += `<b>`;
                        closeTags = `</b>` + closeTags;
                    } else if (tagName === 'i' || tagName === 'em') {
                        openTags += `<i>`;
                        closeTags = `</i>` + closeTags;
                    } else if (tagName === 'u') {
                        openTags += `<u>`;
                        closeTags = `</u>` + closeTags;
                    } else if (tagName === 'color') { // 
                        if (attrs.value) { // <color value=...>
                            openTags += `<color=${attrs.value}>`;
                            closeTags = `</color>` + closeTags;
                        } else {
                            //  <color=#fff>  token  <color ...>
                            //  <color=#fff> 
                            // 
                        }
                    } else if (tagName === 'div' || tagName === 'p') {
                        // 
                        if (result.length > 0 && !result.endsWith('\n')) {
                            result += '\n';
                        }
                        // 
                        closeTags = `\n` + closeTags;
                    } else if (tagName === 'span') {
                        // span  style 
                    }

                    // 3.  img (RichText  <img src=.../>)
                    if (tagName === 'img' && attrs.src) {
                        // RichText  img  src  ImageAtlas  SpriteFrame 
                        //  imageAtlas  resources Cocos Creator  imageAtlas
                        // : <img src='name' width=... height=... align=... />
                        let imgTag = `<img src='${attrs.src}'`;

                        // 
                        // HTML style  width/height 
                        //  style="width: 100px..."  style
                        let width = attrs.width;
                        let height = attrs.height;

                        if (attrs.style) {
                            const style = attrs.style.toLowerCase();
                            const wMatch = style.match(/width:\s*(\d+)/);
                            if (wMatch) width = wMatch[1];
                            const hMatch = style.match(/height:\s*(\d+)/);
                            if (hMatch) height = hMatch[1];
                        }

                        if (width) imgTag += ` width=${width}`;
                        if (height) imgTag += ` height=${height}`;

                        imgTag += ` />`;
                        openTags += imgTag;
                        // img  closeTags
                    }

                    result += openTags;

                    if (!isSelfClosing) {
                        tagStack.push(closeTags);
                    } else {
                        //  closeTags ( style )
                        //  img, br 
                        result += closeTags;
                    }
                }
            } else {
                // 
                let content = token;
                // 1. 
                content = content.replace(/\s+/g, ' ');

                // 2. 
                //  </div>\n<p> 
                if (result.endsWith('\n') && content.startsWith(' ')) {
                    content = content.substring(1);
                }

                // 
                content = content.replace(/&nbsp;/g, ' ').replace(/&lt;/g, '<').replace(/&gt;/g, '>').replace(/&amp;/g, '&');
                result += content;
            }
        }

        return result;
    }
}

@ccclass('CustomHtmlRenderer')
export class CustomHtmlRenderer extends Component {
    @property(Node)
    contentContainer: Node = null!;

    @property({ tooltip: "" })
    maxWidth: number = 0;

    @property({ type: SpriteAtlas, tooltip: " RichText " })
    imageAtlas: SpriteAtlas = null!;

    renderHtml(html: string) {
        if (!this.contentContainer) {
            this.contentContainer = this.node;
        }
        //  RichText 
        let richText = this.contentContainer.getComponent(RichText);
        if (!richText) {
            richText = this.contentContainer.addComponent(RichText);
        }

        //  RichText 
        if (this.maxWidth > 0) {
            richText.maxWidth = this.maxWidth;
            const trans = this.contentContainer.getComponent(UITransform);
            if (trans) trans.width = this.maxWidth;
        }

        if (this.imageAtlas) {
            richText.imageAtlas = this.imageAtlas;
        }

        // 
        richText.string = SimpleHtmlParser.toRichText(html).trim();
    }
}
