import { ActionTypes, ActionPayLoad } from "../actions/counter";
import { CounterInitStateType } from '../types';
import reduxAct from 'redux-act'

const initState: CounterInitStateType = {
  size: 12,
  color: "#fcfcfc"
}

export default reduxAct.createReducer<CounterInitStateType>({
  [ActionTypes.CHANGE_SIZE]: (state, payload: ActionPayLoad<ActionTypes.CHANGE_SIZE>): CounterInitStateType => ({
    ...state,
    size: payload.size
  }),
  [ActionTypes.CHANGE_COLOR]: (state, payload: ActionPayLoad<ActionTypes.CHANGE_COLOR>): CounterInitStateType => {
    return {
      ...state,
      color: payload.color
    }
  },
}, initState);