import { default as redux } from "redux";
import {
  ToastType,
  addToastAction,
  setLoadingAction,
} from "../hall/store/actions/baseBoard";
import { Prefab, sys } from "cc";
import {
  baseBoardView,
  global,
  hallAudio,
  lang,
  sourceManageSeletor,
} from "../hall/globals";
import { SoundPathDefine } from "../hall/sourceDefine/soundDefine";
import { getStore } from "../hall/store";
import Throttler from "./Throttler";
import { sendNativeVibrate } from "../common/bridge";
import {
  Hall_ProhibitPanel,
  IState as HPState,
  IProps as HPProps,
  IEvent as HPEvent,
} from "../hall/components/login_v2/Hall_ProhibitPanel";
import BaseViewModel from "../common/viewModel/BaseViewModel";
import { PrefabPathDefine } from "../hall/sourceDefine/prefabDefine";
import { EffectType } from "./NodeIOEffect";
import { defaultLanguageType } from "../language/languagePkg";
import { config } from "../hall/hallConfig";
import { ENABLE_MOCK, getMockResponse } from "./mockData";

export enum DataVerify {
  ANY,
  ARRAY,
  OBJECT,
  STRING,
  CUSTOME,
}

export const SampleData = {
  NUMBER: 0,
  STRING: "",
  ARRAY: [],
  BOOLEAN: true,
  OBJECT: {},
};

export default class Fetcher<T> {
  constructor(baseUrl: string) {
    this.baseUrl = baseUrl;
    this.dispatch = getStore().dispatch;
  }
  private dispatch: redux.Dispatch;
  private baseUrl: string;

  send(
    url: string,
    sendData: object = {},
    method: "post" | "get" | "delete" = "post",
    headers?: {},
    option?: {
      isLoading?: boolean;
      dataVerify?: DataVerify;
      verifySampleData?: { [key: string]: any };
      /**true */
      isThrottler?: boolean;
    }
  ): Promise<any> {
    console.log(`httpurl:${url},method:${method},sendData:${sendData}`);

    const _option = Object.assign(
      {},
      {
        isLoading: true,
        /**dataVerify = DataVerify.CUSTOMEverifySampleData() */
        dataVerify: DataVerify.ANY,
        verifySampleData: null,
        isThrottler: true,
      },
      option
    );

    // Mock  mock  loading
    if (ENABLE_MOCK && !url.includes("slot/enter") && !url.includes("slot/action")) {
      console.log("Mock  mock  loading");
      //  loading
      if (_option.isLoading) {
        this.dispatch(
          setLoadingAction({ isShow: true, flagId: url.toString() })
        );
      }

      //  loading
      return new Promise((resolve) => {
        setTimeout(() => {
          const mockData = getMockResponse(url, sendData);
          //  loading
          if (_option.isLoading) {
            this.dispatch(
              setLoadingAction({ isShow: false, flagId: url.toString() })
            );
          }
          resolve(mockData);
        }, 100); //  100ms 
      });
    }

    return new Promise((reslove, reject) => {
      const param = {
        method,
        headers: Object.assign(
          {
            "Content-Type": "application/json",
            // Token: sys.localStorage.getItem("token") || "",
            //! commented because server doesn't support Hindi
            // 'Content-Language': defaultLanguageType[config.country].langName
            "Authorization": 20001,
            // "Content-Language": "English",
          },
          headers
        ),
      };
      if (method !== "get") {
        param["body"] = JSON.stringify(sendData);
      }
      console.log("param", param);
      console.log("this.baseUrl + url", this.baseUrl + url);
      const _fetch = () => {
        _option.isLoading &&
          this.dispatch(
            setLoadingAction({ isShow: true, flagId: url.toString() })
          );
        fetch(this.baseUrl + url, param)
          .then((response) => {
            this.dispatch(
              setLoadingAction({ isShow: false, flagId: url.toString() })
            );
            if (response.status === 200) {
              response
                .json()
                .then(({ data, code, status, message }) => {
                  console.log(
                    `httpcode:${code},status:${status},message:${message},data:`,
                    [data][0]
                  );
                  console.log("response data", JSON.stringify(data));
                  if (status === "SUCCESS" || code === 200) {
                    let isDataPass = true;
                    if (_option.dataVerify === DataVerify.ARRAY) {
                      !Array.isArray(data) && (isDataPass = false);
                    } else if (_option.dataVerify === DataVerify.OBJECT) {
                      (typeof data !== "object" || data === null) &&
                        (isDataPass = false);
                    } else if (_option.dataVerify === DataVerify.STRING) {
                      typeof data !== "string" && (isDataPass = false);
                    } else if (_option.dataVerify === DataVerify.CUSTOME) {
                      if (!_option.verifySampleData) {
                        console.warn(
                          ""
                        );
                      } else {
                        !this.validateJson(_option.verifySampleData, data) &&
                          (isDataPass = false);
                      }
                    }
                    if (!isDataPass) {
                      this.dispatch(
                        setLoadingAction({
                          isShow: false,
                          flagId: url.toString(),
                        })
                      );
                      this.dispatch(
                        addToastAction({
                          content: lang.write(
                            (k) => k.InitGameModule.FetcherFaild,
                            {},
                            { placeStr: "json" }
                          ),
                          type: ToastType.ERROR,
                          forceLandscape: false,
                        })
                      );
                      reject(":" + url);
                    } else {
                      reslove(data);
                    }
                  } else if (code === "BIZ-5101") {
                    //
                    this.dispatch(
                      setLoadingAction({ isShow: false, flagId: url.toString() })
                    );
                    if (baseBoardView.mainPanelViewModel) {
                      baseBoardView.mainPanelViewModel.logOut(false);
                    } else {
                      baseBoardView.loginPageV2ViewModel.comp.events.onOpenLoginDialog();
                    }
                  } else if (code === "BIZ-5102") {
                    //IP
                    sourceManageSeletor()
                      .getFileAsync(PrefabPathDefine._PROHIBIT_PANEL, Prefab)
                      .then((fileSource) => {
                        const prohibitViewModel = new BaseViewModel<
                          Hall_ProhibitPanel,
                          HPState,
                          HPProps,
                          HPEvent
                        >("Hall_ProhibitPanel")
                          .mountView(fileSource.source)
                          .setEvent({
                            onCloseHandle: () =>
                              prohibitViewModel.unMount(EffectType.EFFECT1),
                          })
                          .appendTo(baseBoardView.viewNode, {
                            effectType: EffectType.EFFECT2,
                            isModal: true,
                          });
                      });
                  } else {
                    this.dispatch(
                      addToastAction({
                        content: message,
                        type: ToastType.ERROR,
                        forceLandscape: false,
                      })
                    );
                    this.dispatch(
                      setLoadingAction({ isShow: false, flagId: url.toString() })
                    );
                    reject(message);
                  }
                })
                .catch(() => {
                  this.dispatch(
                    addToastAction({
                      content: lang.write(
                        (k) => k.InitGameModule.FetcherFaild,
                        {},
                        { placeStr: "json" }
                      ),
                      type: ToastType.ERROR,
                      forceLandscape: false,
                    })
                  );
                  this.dispatch(
                    setLoadingAction({ isShow: false, flagId: url.toString() })
                  );
                  // console.log("json", response)
                  hallAudio.play(SoundPathDefine.ERROR);
                  reject("json");
                });
            } else {
              this.dispatch(
                addToastAction({
                  content: response.status.toString(),
                  type: ToastType.ERROR,
                  forceLandscape: false,
                })
              );
              this.dispatch(
                setLoadingAction({ isShow: false, flagId: url.toString() })
              );
              hallAudio.play(SoundPathDefine.ERROR);
              reject(response);
            }
          })
          .catch((e) => {
            sendNativeVibrate(100);
            this.dispatch(
              addToastAction({
                content: e.toString(),
                type: ToastType.ERROR,
                forceLandscape: false,
              })
            );
            this.dispatch(
              setLoadingAction({ isShow: false, flagId: url.toString() })
            );
            hallAudio.play(SoundPathDefine.ERROR);
            reject(e);
          });
      };

      if (_option.isThrottler) {
        Throttler.isDebouncerAsync(this.baseUrl + url, 500, true, () => {
          console.log("Debouncer stop", this.baseUrl + url);
          global.hallDispatch(
            addToastAction({
              content: lang.write(
                (k) => k.PersonCenterModule.PersonCenterSumbit,
                {},
                { placeStr: "~" }
              ),
            })
          );
        }).then((isPass) => {
          _fetch();
        });
      } else {
        _fetch();
      }
    });
  }
  /**
   * 
   * @param sampleData 
   * @param data 
   * @returns
   */
  validateJson(sampleData: Object, data: Object) {
    // 
    if (typeof data !== "object" || data === null) {
      return false;
    }
    // 
    for (const key in sampleData) {
      if (sampleData.hasOwnProperty(key)) {
        // 
        if (!(key in data)) {
          console.error(`Missing field: ${key}`);
          return false;
        }
        // 
        let expectedType = typeof sampleData[key];
        let actualType = typeof data[key];
        if (
          actualType !== expectedType &&
          Array.isArray(sampleData[key]) &&
          !Array.isArray(data[key])
        ) {
          console.error(
            `Invalid data type for field ${key}. Expected ${expectedType}, got ${actualType}`
          );
          return false;
        }
      }
    }
    // 
    for (const key in data) {
      if (data.hasOwnProperty(key) && !(key in sampleData)) {
        console.warn(`Unexpected field: ${key}`);
        // false
      }
    }
    return true;
  }
}
